<template>
  <div :class="$root.themeHomeChange === '1' ? 'ehcartsBar_light' : 'ehcartsBar'">
    
    <div class="header">
      <div class="left">
        <img src="@/assets/img/companyCenter/jiantou.png" />
        <span class="headerTitle">{{title}}</span> 
      </div>
      <el-date-picker
        v-model="date"
        size="mini"
        :type="dateType"
        :format="dateType === 'month' ? 'yyyy 年 MM 月' : 'yyyy 年'"
        :value-format="dateType === 'month' ? 'yyyy-MM' : 'yyyy'"
        placeholder="选择日期"
        style="width: 160px;"
      ></el-date-picker>
    </div>
    
    <div class="content">
      <div id="barCharts" ref="barCharts"></div>
    </div>
  </div>
</template>

<script>
  let option = {
    // toolbox: {
    //   right: '20px',
    //   feature: {
    //     dataView: { show: true, readOnly: true, title: '图表数据' },
    //     // magicType: { show: true, type: ['line', 'bar'] },
    //     // restore: { show: true },
    //     saveAsImage: { show: true, title: '保存为图片'}
    //   }
    // },
    tooltip: {
      trigger: 'axis',
      backgroundColor: 'rgba(42, 92, 176, .6)',
      borderColor: "rgba(0, 255, 239, 1.0)",
      textStyle: {
        color: '#00FFEF'
      },
      axisPointer: {
        type: 'cross',
        label: {
          formatter: params => {
            // window.console.log(params);
            return params.axisDimension == 'x' ? params.value + '供电分局' : params.value.toFixed(2);
          }
        }
      },
      formatter: params => {
        // window.console.log(params);
        let html = [
          `<p style="padding-bottom: 4px; margin-bottom: 4px; border-bottom: 1px dashed ${params[0].color};">`,
          `<strong>${params[0].name}供电分局</strong>`,
          `</p>`,
          `<p style="text-align: left;">`,
          `<span style="color: ${params[0].color};">● </span>`,
          `${params[0].seriesName}：<strong>${params[0].value}</strong>`,
          `</p>`
        ];
        return html.join("");
      }
    },
    grid: {
      top: '20%',
      bottom: '15%',
      left: '5%',
      right: '5%',
    },
    legend: [],
    xAxis: [
      {
        type: 'category',
        // name: '单位: 供电分局',
        nameRotate: '-90',
        axisPointer: {
          type: 'shadow'
        },
        axisLabel: {
          color: '#fff',
          fontWeight: 'bold',
          interval: 0,
        },
        nameTextStyle: {
          color: "rgba(66, 134, 206, 1.0)",
          padding: [0, 50, 0, 0]
        }
      }
    ],
    yAxis: [
      {
        type: 'value',
        name: '单位：XX',
        min: 0,
        max: 100,
        interval: 20,
        splitLine: {
          lineStyle: {
            color: "rgba(0, 102, 185, .8)"
          }
        },
        axisLabel: {
          color: '#0094d3',
          formatter: '{value}'
        },
        nameTextStyle: {
          color: "rgba(66, 134, 206, 1.0)"
        }
      }
    ],
    series: [
      {
        name: '当前评分',
        type: 'bar',
        barWidth: "50%",
        itemStyle: {
          // normal: {
          label: {
            show: false,
            position: "top",
            textStyle: {
              fontWeight: "bolder",
              fontSize: "12",
              color: '#00FFEF'
            }
          },
          borderRadius: [2, 2, 0, 0],
          color: new echarts.graphic.LinearGradient(
            0, 0, 0, 1,
            [
              {offset: 0, color: '#00FFEF'},
              {offset: 1, color: '#007EFF'}
            ]
          )
          // }
        },
        data: [
          78, 54, 79, 60, 37, 76, 75, 37, 56, 85, 69, 78, 57, 76, 85, 69, 78, 57, 76
        ]
      }
    ]
  };
  import * as echarts from "echarts";
  import bus from '@/utils/bus';
  export default {
    name: "ehcartsBar",
    props: {
      title: {
        type: String,
        default: () => {
          return '智能组件';
        },
      },
      dateType: {
        type: String,
        default: () => {
          return 'year';
        },
      },
      data: {
        type: Array,
        default: () => {
          return [];
        },
      }
    },
    data(){
      return {
        time: null,
        date: '',
        mychart: '',
        outSideOption: {},
        option: {}
      }
    },
    mounted() {
      this.time = setInterval(()=>{
        this.mychart.resize()
      },300);
      // this.init();
    },
    created() {},
    watch: {
      '$root.themeHomeChange'() {
        this.init(this.outSideOption);
      }
    },
    methods: {
      dataSet(outSideOption = {}) {
        this.option = JSON.parse(JSON.stringify(option))
        // 初始化时间选择框数据
        let nowDate = new Date();
        let year = nowDate.getFullYear(); 
        let month = nowDate.getMonth()+1;
        month = (month < 10 ? "0" + month : month); 
        this.date = year + '-' + month;
                
        // window.console.log(outSideOption.series.data.sort((a,b) => {return b.value - a.value}))
        
        // 组件外配置项注入
        this.option.xAxis[0].data = outSideOption.series.data.sort((a,b) => {return b.value - a.value}).map(item => { return item.name });
        this.option.series[0].data = outSideOption.series.data.sort((a,b) => {return b.value - a.value});
        this.option.series[0].itemStyle.color = outSideOption.series.itemColor;
        this.option.series[0].name = outSideOption.series.name;
        // this.option.yAxis[0] = outSideOption.yAxis;
        this.option.yAxis[0].min = outSideOption.yAxis.min;
        this.option.yAxis[0].max = outSideOption.yAxis.max || ((params) => {
          // window.console.log(params);
          return params.max * 2;
        });
        this.option.yAxis[0].splitLine.lineStyle.color = outSideOption.yAxis.splitLineColor;
        if(outSideOption.yAxis.interval) {
          this.option.yAxis[0].interval = outSideOption.yAxis.interval;
        } else {
          this.option.yAxis[0].minInterval = outSideOption.yAxis.minInterval;
        }
        this.option.yAxis[0].name = outSideOption.yAxis.name;
        
        if(outSideOption.xAxis.additionalText) {
          this.option.tooltip.formatter  = (params) => {
            // window.console.log(params)
            let html = [
              `<p style="padding-bottom: 4px; margin-bottom: 4px;">`,
              `<strong>${params[0].name} ${outSideOption.xAxis.additionalText}</strong>`,
              `</p>`,
              `<p style="text-align: left;">`,
              `<span style="color: ${params[0].color};">● </span>`,
              `${params[0].seriesName}：<strong>${params[0].value}</strong>`,
              `</p>`
            ];
            return html.join("");
          };
        }
        
        this.setTheme();
      },
      setTheme(){
        // window.console.log(23123);
        //样式区分
        if (this.$root.themeHomeChange === '1'){  //深色背景
          this.option.legend.forEach(item => {
            item.textStyle = {color: '#333'}
          })
          // this.option.legend.textStyle = {color: '#333333'}
          this.option.xAxis[0].axisLabel.textStyle = {
            color: "#333333",
            margin: 15,
            fontWeight: 'bold',
          }
          this.option.yAxis.forEach(item => {
            item.axisLabel.textStyle = {
              color: "#333333",
              margin: 15,
              // fontWeight: 'bold',
            }
          })
        }else if(this.$root.themeHomeChange==='2'){ //浅色背景
          this.option.legend.forEach(item => {
            item.textStyle = {color: '#ffffff'}
          })
          // this.option.legend.textStyle = {color: '#ffffff'};
          this.option.xAxis[0].axisLabel.textStyle = {
            color: "#ffffff",
            margin: 15,
            fontWeight: 'bold',
          }
          this.option.yAxis.forEach(item => {
            item.axisLabel.textStyle = {
              color: "#66B9FF",
              margin: 15,
              // fontWeight: 'bold',
            }
          })
        }
      },
      init(outSideOption = {}) {
        this.outSideOption = JSON.parse(JSON.stringify(outSideOption));
        this.dataSet(outSideOption);
        
        this.mychart = echarts.init(this.$refs.barCharts);
        
        this.mychart.setOption(this.option);
        
        window.addEventListener("resize", ()=>{
          this.mychart.resize()
        });
      }
    },
    beforeDestroy() {
      bus.$off('isCollapse');
      clearInterval(this.time);
      window.removeEventListener("resize", () => {
        this.mychart.resize();
      });
    }
  }
</script>

<style scoped lang="scss">
  .ehcartsBar{
    width: calc(100% - 20px);
    height: calc((100% - 125px) / 3);
    display: flex;
    flex-wrap: wrap;
    // height: 250px;
    padding: 10px;
    margin-top: 15px;
    background: url(../../assets/img/riskControlCenter/jhxhgrw.png) no-repeat;
    background-size: 100% 100%;
    /deep/ .el-date-editor .el-input__inner {
      background-color: #041338;
      color: #d3d3d3;
      border: 1px solid #2a5cb0;
      border-radius: 3px;
      text-align: center;
    }
    .header {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 5px;
      height: 32px;
      .left {
        display: flex;
        align-items: center;
        // padding-left: 10px;
        .headerTitle {
          font-family: MicrosoftYaHei-Bold;
          font-size: 17px;
          color: #00F8FF;
          font-weight: 600;
          margin-left: 5px;
        }
      }
    }
    .content {
      width: 100%;
      height: calc(100% - 42px);
      display: flex;
      div {
        width: 100%;
        height: 100%;
        position: relative;
      }
    }
  }
  
  // 浅色主题
  .ehcartsBar_light {
    width: calc(100% - 20px);
    height: calc((100% - 125px) / 3);
    display: flex;
    flex-wrap: wrap;
    // height: 250px;
    padding: 10px;
    margin-top: 15px;
    // background: url(../../assets/img/riskControlCenter/jhxhgrw.png) no-repeat;
    // background-size: 100% 100%;
    background: #fff;
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
    border-radius: 6px;
    background-size: 100% 100%;
    position: relative;
    overflow: hidden;
    &::after {
      content: "";   
      display: block;
      width: 50px;   
      height: 5px;
      background: #4085fa;
      position: absolute;
      top: -5px;
      left: -15px;
      transform: rotate(-45deg);
      // border-radius: 50px 0;
    }
    &::before {
      content: "";   
      display: block;
      width: 50px;   
      height: 5px;
      background: #4085fa;
      position: absolute;
      bottom: -5px;
      right: -15px;
      transform: rotate(-45deg);
      // border-radius: 50px 0;
    }
    /deep/ .el-date-editor .el-input__inner {
      background-color: #fff;
      color: #333;
      border: 1px solid #bbb;
      border-radius: 3px;
      text-align: center;
    }
    .header {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 5px;
      height: 32px;
      .left {
        display: flex;
        align-items: center;
        // padding-left: 10px;
        .headerTitle {
          font-family: MicrosoftYaHei-Bold;
          font-size: 17px;
          color: #4389F9;
          font-weight: 600;
          margin-left: 5px;
        }
      }
    }
    .content {
      width: 100%;
      height: calc(100% - 42px);
      display: flex;
      div {
        width: 100%;
        height: 100%;
        position: relative;
      }
    }
  }
</style>